.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 14rem;
    text-align: center;
    border-radius: 1rem;
    height: 200px;
}

.card img {
    width: 6rem;
}

.card:hover {
    box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.2);
    font-size: 1.05rem;
    cursor: pointer;
}

.card:hover img {
    width: 6.5rem;
}